export interface VhQuoteReceiptDetail {
    id_bill?: string;
    _id?: string;
    name?: string;
    id_product?: string;
    quantity?: number;
    price?: number;
    price_origin?: number;
    unit?: string;
    units?: any[];
    ratio?: number;
    ptype?: number;
    cbtype?: number;
    combos?: VhQuoteReceiptDetailCombo[];
    free?: boolean,
    earning_points_product?: number;
    products_gift?: any;
    products?: any;
    id_promotion?: any;
    pmtype?: number;
    promotions_maximum?: any;
    id_subproduct?: string;
    lots?: any[];
    lot_number?: string;
    id_lotproduct?: string;
}
export interface VhWarrantyQuote {
    id_bill?: string;
    _id?: string;
    quantity?: number;
    content: string;
    type: 1 | 2;
}
export interface VhQuoteReceipt {
    bill_type: 18 | 19 | 20 | 21
    payment_type: 1 | 2 | 3
    id_customer: 'id_retail' | string;
    id_branch: string;
    id_employee: string;
    id_technician: string;
    id_wallet: 'id_cash' | string;
    date_schedule: string;
    date_reminder: string;
    date: string;
    total: number;
    payment: number;
    tax: number;
    discount: number;
    fee: number;
    note: string;
    _id?: string;

    bill_code?: string
    partner_name?: string;
    phone?: string;
    payment_name?: string;
    retail_name?: string,
    retail_phone?: string,
    retail_address?: string
}
export interface VhQuoteReceiptDetailCombo {
    id_product: string;
    id_subproduct?: string;
    price: number;
    quantity: number;
    name?: string;
    ptype: number;
    unit?: string;
    units?: any[];
    ratio: number;
    id_lotproduct?: string;
    lots?: any[];
}
export class VhQuoteReceipts {
    private invoice: VhQuoteReceipt = {
        payment_type: 1,
        date: (new Date()).toISOString(),
        date_schedule: (new Date(Date.now() + 86400000 * 3)).toISOString(),
        date_reminder: (new Date(Date.now() + 86400000)).toISOString(),
        id_customer: 'id_retail',
        id_technician: null,
        id_branch: null,
        id_employee: null,
        id_wallet: 'id_cash',
        total: 0,
        payment: 0,
        tax: 0,
        discount: 0,
        fee: 0,
        bill_type: null,
        note: '',
        _id: null
    };
    private subTotal?: number = 0;
    private taxValue?: number = 0;
    private invoiceDetail?: VhQuoteReceiptDetail[] = [];
    private warrantyProd: VhWarrantyQuote[] = []
    private warrantyNote: VhWarrantyQuote[] = []

    private warrantyDel: VhWarrantyQuote[] = []
    constructor(invoice: any = null, invoiceDetail: any[] = [], warranty: any[] = []) {

        if (invoice) {
            this.invoice.bill_code = invoice['bill_code'] ? invoice['bill_code'] : null
            this.invoice.payment_type = invoice['payment_type'] ? invoice['payment_type'] : 1
            this.invoice.id_customer = invoice['id_customer'] ? invoice['id_customer'] : 'id_retail'
            this.invoice.id_branch = invoice['id_branch'] ? invoice['id_branch'] : null
            this.invoice.id_employee = invoice['id_employee'] ? invoice['id_employee'] : null
            this.invoice.id_technician = invoice['id_technician'] ? invoice['id_technician'] : null
            this.invoice.id_wallet = invoice['id_wallet'] ? invoice['id_wallet'] : 'id_cash'
            this.invoice.total = invoice['total'] ? invoice['total'] : 0
            this.invoice.payment = invoice['payment'] ? invoice['payment'] : 0
            this.invoice.tax = invoice['tax'] ? invoice['tax'] : 0
            this.invoice.discount = invoice['discount'] ? invoice['discount'] : 0
            this.invoice.fee = invoice['fee'] ? invoice['fee'] : 0
            this.invoice.bill_type = invoice['bill_type'] ? invoice['bill_type'] : null

            this.invoice.date = invoice['date'] ? invoice['date'] : (new Date()).toISOString()
            this.invoice.date_reminder = invoice['date_reminder'] ? invoice['date_reminder'] : (new Date(Date.now() + 86400000)).toISOString()
            this.invoice.date_schedule = invoice['date_schedule'] ? invoice['date_schedule'] : (new Date(Date.now() + 86400000 * 3)).toISOString()
            this.invoice.note = invoice['note'] ? invoice['note'] : ''
            this.invoice._id = invoice['_id'] ? invoice['_id'] : null
            this.invoiceDetail = invoice['invoiceDetail'] ? invoice['invoiceDetail'] : []

            this.taxValue = this.invoice.total * (this.invoice.tax / 100)
            if (invoice['id_customer'] == 'id_retail') {
                this.invoice.retail_name = invoice['retail_name'] || ''
                this.invoice.retail_phone = invoice['retail_phone'] || ''
                this.invoice.retail_address = invoice['retail_address'] || ''
            }
        }

        if (invoiceDetail) {
            this.invoiceDetail = invoiceDetail
            this.subTotal = this.invoiceDetail.reduce((prev: number, next) => prev + next.quantity * next.price, 0)
        }

        if (warranty) {
            this.warrantyProd = warranty.filter(item => item.type == 1)
            this.warrantyNote = warranty.filter(item => item.type == 2)
        }
    }
    /******************************Xu ly invoice *******************************/
    public getID(): string {
        return this.invoice._id;
    }
    public setID(value: string) {
        this.invoice._id = value;
    }
    public getBillType(): 18 | 19 | 20 | 21 {
        return this.invoice.bill_type
    }
    public setBillType(value: 18 | 19 | 20 | 21) {
        this.invoice.bill_type = value
    }
    public getDate(): string {
        return this.invoice.date
    }
    public setDate(value: Date | string) {
        this.invoice.date = typeof (value) == 'string' ? value : value.toISOString()
    }
    public getDateSchedule(): string {
        return this.invoice.date_schedule
    }
    public setDateSchedule(value: Date | string) {
        this.invoice.date_schedule = typeof (value) == 'string' ? value : value.toISOString()
    }
    public getDateReminder(): string {
        return this.invoice.date_reminder
    }
    public setDateReminder(value: Date | string) {
        this.invoice.date_reminder = typeof (value) == 'string' ? value : value.toISOString()
    }
    public getBillCode(): string {
        return this.invoice.bill_code
    }
    public setBillCode(value: string) {
        this.invoice.bill_code = value;
    }
    public getPaymentType(): 1 | 2 | 3 {
        return this.invoice.payment_type
    }
    public setPaymentType(value: 1 | 2 | 3) {
        this.invoice.payment_type = value
    }
    public getIdPartner(): string {
        return this.invoice.id_customer
    }
    public setIdPartner(value: string) {
        this.invoice.id_customer = value
    }
    public getIdEmployee(): string {
        return this.invoice.id_employee
    }
    public setIdEmployee(value: string) {
        this.invoice.id_employee = value
    }
    public getIdTechnician(): string {
        return this.invoice.id_technician
    }
    public setIdTechnician(value: string) {
        this.invoice.id_technician = value
    }
    public getIdBranch(): string {
        return this.invoice.id_branch
    }
    public setIdBranch(value: string) {
        this.invoice.id_branch = value
    }
    public getIdWallet(): string {
        return this.invoice.id_wallet
    }
    public setIdWallet(value: string) {
        this.invoice.id_wallet = value
    }
    public getNote(): string {
        return this.invoice.note
    }
    public setNote(value: string) {
        this.invoice.note = value
    }
    public getTax(): number {
        return this.invoice.tax
    }
    public setTax(value: number) {
        this.invoice.tax = value
        this.setTaxValue()
    }
    public getTaxValue(): number {
        return this.taxValue
    }
    public setTaxValue() {
        this.taxValue = this.invoice.total * (this.invoice.tax / 100)
        this.setPayment()
    }
    public getDiscount(): number {
        return this.invoice.discount
    }
    public setDiscount(value: number) {
        this.invoice.discount = value
        this.setTotal()
    }
    public getFee(): number {
        return this.invoice.fee
    }
    public setFee(value: number) {
        this.invoice.fee = value
        this.setTotal()
    }
    public getSubTotal(): number {
        return this.subTotal
    }
    public setSubTotal() {
        this.subTotal = this.invoiceDetail.reduce((prev: number, next) => prev + next.quantity * next.price, 0)
        this.setTotal()
    }
    public getTotal(): number {
        return this.invoice.total
    }
    public setTotal() {
        this.invoice.total = this.getSubTotal() - this.getDiscount() + this.getFee()
        this.setTaxValue()
    }
    public getTotalATax(): number {
        return this.getTotal() + this.getTaxValue()
    }
    public getPayment(): number {
        return this.invoice.payment
    }
    public setPayment(value?: number) {

        if (this.invoice.payment_type == 2) {
            this.invoice.payment = 0
        } else {
            if (value != null)
                this.invoice.payment = value
            else
                this.invoice.payment = this.getTotal() + this.getTaxValue()
        }
    }
    public getRetailName() {
        return this.invoice.retail_name
    }
    public setRetailName(retail_name: string) {
        this.invoice.retail_name = retail_name
    }
    public getRetailPhone() {
        return this.invoice.retail_phone
    }
    public setRetailPhone(retail_phone: string) {
        this.invoice.retail_phone = retail_phone
    }
    public getRetailAddress() {
        return this.invoice.retail_address
    }
    public setRetailAddress(retail_address: string) {
        this.invoice.retail_address = retail_address
    }
    /******************************Xu ly invoice detail *******************************/
    /** Thêm sản phẩm vô bill detail */
    public addInvoiceDetail(value: any, new_price?: number) {
        if (value['id_subproduct']) {
            if (value.lot_number) {
                let index = this.invoiceDetail.findIndex(detail => detail['lot_number'] == value['lot_number'] && detail['id_lotproduct'] == value['id_lotproduct'])
                if (index != -1) {
                    if (this.invoiceDetail[index]['quantity'] < 9999999999) this.invoiceDetail[index]['quantity'] += 1
                } else {
                    this.addBill_Detail(value)
                }
            }
            else {
                let index = this.invoiceDetail.findIndex(detail => detail['id_subproduct'] == value['id_subproduct'] && detail['ratio'] == value['ratio'])
                if (index != -1) {
                    if (this.invoiceDetail[index]['quantity'] < 9999999999) this.invoiceDetail[index]['quantity'] += 1
                } else {
                    this.addBill_Detail(value)
                }
            }
        }
        else {
            if (value.lot_number) {
                let index = this.invoiceDetail.findIndex(detail => detail['lot_number'] == value['lot_number'] && detail['id_lotproduct'] == value['id_lotproduct'])
                if (index != -1) {
                    if (this.invoiceDetail[index]['quantity'] < 9999999999) this.invoiceDetail[index]['quantity'] += 1
                } else {
                    this.addBill_Detail(value)
                }
            }
            else {
                let index = this.invoiceDetail.findIndex(detail => detail['id_product'] == value['_id'] && detail['ratio'] == value['ratio'])
                if (index != -1) {
                    if (this.invoiceDetail[index]['quantity'] < 9999999999) this.invoiceDetail[index]['quantity'] += 1
                } else {
                    this.addBill_Detail(value)
                }
            }
        }

    }

    addBill_Detail(value: any, new_price?: number) {
        let detail: any = {
            name: value.name,
            id_product: value._id,
            quantity: 1,
            ptype: value.type,
            unit: value.unit,
            units: value.units ? value.units : [],
            ratio: value.ratio ? value.ratio : 1,
            price: new_price != null ? new_price : value.price,
            price_origin: value.price,
            id_category: value.id_category,
            earning_points_product: value.earning_points_product ? value.earning_points_product : 0,
        }

        if (value.type == 5) {
            detail['cbtype'] = 5;
            detail['combos'] = [...value.combos.map(item => {
                let product: VhQuoteReceiptDetailCombo = {
                    id_product: item.id_product,
                    name: item.name,
                    ptype: item.type,
                    price: item.price,
                    quantity: item.quantity,
                    unit: item.unit,
                    units: item.units,
                    ratio: item.ratio ? item.ratio : 1,
                    lots: value.lots ? value.lots : [],
                    id_lotproduct: value.id_lotproduct,
                    
                }
                if (item.lots) product['lots'] = item.lots;
                if (item.id_lotproduct) product['id_lotproduct'] = item.id_lotproduct;
                if (item.id_subproduct) product['id_subproduct'] = item.id_subproduct;
                return product;
            })]
        }
        if (value.lot_number) detail['lot_number'] = value.lot_number;
        if (value.lots) detail['lots'] = value.lots;
        if (value.id_lotproduct) detail['id_lotproduct'] = value.id_lotproduct;
        if (value.id_subproduct) detail['id_subproduct'] = value.id_subproduct;

        this.invoiceDetail.unshift(detail)

        this.setSubTotal()
    }

    addBill_detail_promotion(value) {
        this.invoiceDetail.push(value)
    }
    /** Thêm sản phẩm vào bill detail */
    public addInvoiceDetailMutilLine(value: any, new_price?: any) {
        let detail: any = {
            name: value.name,
            id_product: value._id,
            quantity: 1,
            ptype: value.type,
            unit: value.unit,
            units: value.units ? value.units : [],
            ratio: value.ratio ? value.ratio : 1,
            price: new_price != null ? new_price : value.price,
            price_origin: value.price,
            id_category: value.id_category,
            earning_points_product: value.earning_points_product ? value.earning_points_product : 0,
        }

        if (value.type == 5) {
            detail['cbtype'] = 5;
            detail['combos'] = [...value.combos.map(item => {
                let product: VhQuoteReceiptDetailCombo = {
                    id_product: item.id_product,
                    name: item.name,
                    ptype: item.type,
                    price: item.price,
                    quantity: item.quantity,
                    unit: item.unit,
                    units: item.units,
                    ratio: item.ratio ? item.ratio : 1,
                    lots: value.lots ? value.lots : [],
                    id_lotproduct: value.id_lotproduct,
                    
                }
                if (item.lots) product['lots'] = item.lots;
                if (item.id_lotproduct) product['id_lotproduct'] = item.id_lotproduct;
                if (item.id_subproduct) product['id_subproduct'] = item.id_subproduct;
                return product;
            })]
        }
        if (value.lot_number) detail['lot_number'] = value.lot_number;
        if (value.lots) detail['lots'] = value.lots;
        if (value.id_lotproduct) detail['id_lotproduct'] = value.id_lotproduct;
        if (value.id_subproduct) detail['id_subproduct'] = value.id_subproduct;

        this.invoiceDetail.unshift(detail)

        this.setSubTotal()
    }
    public removeInvoiceDetail(id: string) {
        let index = this.invoiceDetail.findIndex(detail => detail['id_product'] == id)
        if (index != -1) {
            this.invoiceDetail.splice(index, 1);
            this.setSubTotal()
        }
    }

    public getInvoiceDetail() {
        return this.invoiceDetail;
    }

    /******************************Xu ly warranty note *******************************/
    public addWarrantyProd() {
        const add = () => {
            let item: VhWarrantyQuote = { content: '', quantity: 1, type: 1, id_bill: this.invoice._id }
            if (this.getID()) item['id_bill'] = this.getID()
            this.warrantyProd.unshift(item)
        }
        if (this.warrantyProd.length) {
            if (this.warrantyProd[0].content) add()
        } else add()
    }
    public removeWarrantyProd(index: number, item: VhWarrantyQuote) {
        if (index == 0) {
            if (this.warrantyProd.length > 1) this.warrantyProd.splice(index, 1)
            else if (this.warrantyProd[index].content.length) this.warrantyProd[0].content = ''
        } else this.warrantyProd.splice(index, 1)

        if (item._id) this.warrantyDel.push(item)
    }

    public addWarrantyNote() {
        const add = () => {
            let item: VhWarrantyQuote = { content: '', type: 2, id_bill: this.invoice._id }
            if (this.getID()) item['id_bill'] = this.getID()
            this.warrantyNote.unshift(item)
        }
        if (this.warrantyNote.length) {
            if (this.warrantyNote[0].content) add()
        } else add()
    }
    public removeWarrantyNote(index: number, item: VhWarrantyQuote) {
        if (index == 0) {
            if (this.warrantyNote.length > 1) this.warrantyNote.splice(index, 1)
            else if (this.warrantyNote[index].content.length) this.warrantyNote[0].content = ''
        } else this.warrantyNote.splice(index, 1)
        if (item._id) this.warrantyDel.push(item)
    }

    public getWarrantyProd() {
        return this.warrantyProd
    }

    public getWarrantyNote() {
        return this.warrantyNote
    }

    public getWarranty() {
        return this.warrantyNote.concat(this.warrantyProd).filter(item => item.content)
    }
    public getDelWarranty() {
        return this.warrantyDel
    }

    public getUpdateWarranty() {
        return this.warrantyNote.concat(this.warrantyProd).filter(item => item.content && item._id)
    }
    public getCreateWarranty() {
        return this.warrantyNote.concat(this.warrantyProd).filter(item => {
            if (!item.id_bill) item.id_bill = this.getID()
            if (item.content && !item._id) return true
            else return false
        })
    }
    public getInvoiceDetailCombos(idDetail: string): VhQuoteReceiptDetailCombo[] {
        let detailBill: VhQuoteReceiptDetail = this.invoiceDetail.find(detail => detail._id == idDetail)
        return detailBill ? detailBill.combos : null;
    }
    /******************************create - update - delete invoice / detail *******************************/
    public getCreateUpdateInvoice(bill_type: 18 | 19 | 20 | 21 = null) {
        if (bill_type) this.setBillType(bill_type)
        return this.invoice
    }
    public getCreateUpdateInvoiceDetail(): VhQuoteReceiptDetail[] {
        return this.invoiceDetail.map(item => {
            if (!item.pmtype) {
                let detail: VhQuoteReceiptDetail = {
                    id_product: item.id_product,
                    ptype: item.ptype,
                    price: item.price,
                    quantity: item.quantity,
                    earning_points_product: item.earning_points_product,
                    ratio: item.ratio,
                }
                if (item.id_lotproduct) detail['id_lotproduct'] = item.id_lotproduct
                if (item.id_subproduct) detail['id_subproduct'] = item.id_subproduct
                if (item.price < item.price_origin) detail['price_origin'] = item.price_origin
                if (item.price == item.price_origin) detail['price_origin'] = item.price

                if (item.ptype == 5) {
                    detail['combos'] = item.combos.map(combo => {
                        let product: VhQuoteReceiptDetailCombo = {
                            id_product: combo.id_product,
                            price: combo.price,
                            quantity: combo.quantity,
                            ptype: combo.ptype,
                            ratio: combo.ratio ? combo.ratio : 1,
                        }
                        if (combo.id_lotproduct) product['id_lotproduct'] = combo.id_lotproduct
                        if (combo.id_subproduct) product['id_subproduct'] = combo.id_subproduct
                        return product;
                    })
                }
                return detail
            }
            else {
                let detail = {
                    id_promotion: item.id_promotion,
                    pmtype: item.pmtype,
                }
                if (item.products_gift) {
                    if (item.products_gift.filter(ele => ele.choose).length) {
                        detail['products_gift'] = item.products_gift.filter(ele => ele.choose).map(e => {
                            let prod = {
                                id_product: e.id_product,
                                price: e.price,
                                price_origin: e.price_origin,
                                quantity: e.quantity,
                                ptype: e.ptype,
                                earning_points_product: e.earning_points_product,
                                ratio: e.ratio
                            }
                            if (e.ptype == 5) {
                                prod['combos'] = e.combos.map(combo => {
                                    let product: VhQuoteReceiptDetailCombo = {
                                        id_product: combo.id_product,
                                        price: combo.price,
                                        quantity: combo.quantity,
                                        ptype: combo.ptype,
                                        ratio: combo.ratio ? combo.ratio : 1,
                                    }
                                    if (combo.id_lotproduct) product['id_lotproduct'] = combo.id_lotproduct
                                    if (combo.id_subproduct) product['id_subproduct'] = combo.id_subproduct
                                    return product;
                                })
                            }
                            if (e.id_lotproduct) prod['id_lotproduct'] = e.id_lotproduct
                            if (e.id_subproduct) prod['id_subproduct'] = e.id_subproduct
                            return prod
                        })
                        return detail
                    }
                }
                else {
                    detail['products'] = item.products.map(e => {
                        let prod = {
                            id_product: e.id_product,
                            price: e.price,
                            price_origin: e.price_origin,
                            quantity: e.quantity,
                            ptype: e.ptype,
                            id_category: e.id_category,
                            earning_points_product: e.earning_points_product,
                            ratio: e.ratio
                        }
                        if (e.ptype == 5) {
                            prod['combos'] = e.combos.map(combo => {
                                let product: VhQuoteReceiptDetailCombo = {
                                    id_product: combo.id_product,
                                    price: combo.price,
                                    quantity: combo.quantity,
                                    ptype: combo.ptype,
                                    ratio: combo.ratio ? combo.ratio : 1,
                                }
                                if (combo.id_lotproduct) product['id_lotproduct'] = combo.id_lotproduct
                                if (combo.id_subproduct) product['id_subproduct'] = combo.id_subproduct
                                return product;
                            })
                        }
                        if (e.id_lotproduct) prod['id_lotproduct'] = e.id_lotproduct
                        if (e.id_subproduct) prod['id_subproduct'] = e.id_subproduct
                        return prod
                    })
                    return detail
                }

            }
        }).filter(item => item)
    }
}
